@import url(https://fonts.googleapis.com/css?family=KoHo:400,500&display=swap);
body{
    margin-top: 0px;
    margin-right: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    height: 100%;
    overflow-x: hidden;
    background-color:#080808;
}

html{
    scroll-behavior: smooth;
    height: 100%;
}

#root{
    height: 100%;
    overflow-y: hidden;
    overflow-x: hidden;
}



.App{
    overflow-x: hidden;
    overflow-y: hidden;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


.navigation-bar{
    height: 50px;
    display: flex;
    width: 1200px;
    margin: 0 auto;
    justify-content: space-between;
}

@media screen and (max-width: 1200px) {
    .navigation-bar{
    width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .navigation-bar{
    justify-content: center;
    }
}



.navigation-bar .nav-tab {
    display:inline-block;
    margin-right: 20px;
    min-width: 80px;
    height: 40px;
    color: white;
    border-radius: 5px;
    margin-left: 8px;
   
    text-align:center;
    z-index: 2;
 
    
}

.navigation-bar .nav-tab.active{
    font-size: 1.2em;
    font-weight: 800;
    color: white;
    border-bottom: 2px solid #c6e8f2;
}




.navigation-bar .nav-text {
    display:inline-block;
    font-family: 'KoHo', sans-serif;
    font-weight: 500;
    font-size: 1.1em;
   
 }


.navigation-bar .nav-tab:hover {
  
    font-size: 1.2em;
    font-weight: 800;
    color: white;
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
   
}

.navigation-bar a{
    color: white;
    text-decoration: none;
}

.navigation-bar a:hover{
    color: white;
}

.not-found{
    width: 100%;
    height: 100%;
    background: #001920;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'KoHo', sans-serif;
}

.bigfoot{
    width: 500px;
}

.projects-header{
    color: white;
    font-family: 'KoHo', sans-serif;
    font-weight: 500;
    font-size: 1.5em;
    text-align: center;
    -webkit-margin-before: 1em;
            margin-block-start: 1em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
}


@media screen and (max-width: 650px) {
    .footer-container_top{
    display: none;
    }
}


.footer-icon-container_top{
    margin-top: 5px;
    margin-left: 20px;
    width: 200px;
    display: flex;
    justify-content: space-between;
}

.png{
    height: 35px;
    width: 35px;
}




